export const capitalize = word => {
  if (word === null || typeof word === "undefined") {
    return word;
  }
  return word.charAt(0).toUpperCase() + word.slice(1);
};

export const shouldSectionUpdate = (props, nextProps, section) => {
  const onCurSection =
    nextProps.states.global.getIn(["section", "changingTo"]) === section ||
    nextProps.states.global.getIn(["section", "cur"]) === section;
  const languageChanged = props.language !== nextProps.language;
  if (onCurSection || languageChanged) {
    return true;
  }
  return false;
};

export const shouldPageUpdate = (props, nextProps, page) => {
  const onCurPage =
    nextProps.states.global.getIn(["page", "changingTo"]) === page ||
    nextProps.states.global.getIn(["page", "cur"]) === page;
  const languageChanged = props.language !== nextProps.language;
  if (onCurPage || languageChanged) {
    return true;
  }
  return false;
};

export const isTouchDevice = () => {
  return "ontouchstart" in document.documentElement;
};

export const getQueryParamByName = (name, url) => {
  if (!url) url = window.location.href;
  name = name.replace(/[\[\]]/g, "\\$&");//eslint-disable-line
  var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
    results = regex.exec(url);
  if (!results) return null;
  if (!results[2]) return "";
  return decodeURIComponent(results[2].replace(/\+/g, " "));
};

export const isElectron = () => {
  // Renderer process
  if (
    typeof window !== "undefined" &&
    typeof window.process === "object" &&
    window.process.type === "renderer"
  ) {
    return true;
  }

  // Main process
  if (
    typeof process !== "undefined" &&
    typeof process.versions === "object" &&
    !!process.versions.electron
  ) {
    return true;
  }

  // Detect the user agent when the `nodeIntegration` option is set to true
  if (
    typeof navigator === "object" &&
    typeof navigator.userAgent === "string" &&
    navigator.userAgent.indexOf("Electron") >= 0
  ) {
    return true;
  }

  return false;
}
