import { fromJS } from "immutable";
import { getPage, getSection } from "./global.selectors.js";

import { isElectron,getQueryParamByName } from "clientUtils";

const _ = {
  flatten: require("lodash/flatten")
};

const sections = ["about", "creations", "contact", "resume"];
const pages = {
  about: ["intro", "passion", "career"],
  creations: ["election2016", "dashboard", "meteorsim"],
  contact: [],
  resume: []
};

console.log();
const availableLangs = ["cn","en"];
let lang = getQueryParamByName("lang");
lang = lang === null || lang === "" || !availableLangs.includes(lang)? null: lang;

const initialState = fromJS({
  runningInElectron: isElectron(),
  routeTransitionState: null, // ["entering","entered","exiting","exited"]
  language: lang || window.localStorage.getItem("language") || "en",
  section: {
    changingFrom: null,
    changingTo: null,
    prev: null,
    cur: null
  },
  page: {
    changingFrom: null,
    changingTo: null,
    prev: null,
    cur: null
  },
  pages: pages,
  sections: sections,
  pathNames: _.flatten(
    sections.map((section, i) => {
      const temp = pages[section].map(page => `/${section}/${page}`);
      temp.unshift(`/${section}`);
      return temp;
    })
  )
});

export { initialState };
export default { initialState };
