import React, { Component } from "react";
import PropTypes from "prop-types";
import { Router, Route, Link, browserHistory, IndexRoute } from "react-router";
import classNames from "classnames";

import Slide from "uiComponents/Slide/Slide.js";
import { GlobalSelectors } from "reduxComponents/global";

import PageNavigation from "uiComponents/PageNavigation/PageNavigation.js";
import SectionTitle from "uiComponents/SectionTitle/SectionTitle.js";
import LocalizationEnhancer from "uiComponents/LocalizationEnhancer/LocalizationEnhancer.js";
import { shouldSectionUpdate } from "clientUtils";

import { Election2016, Dashboard, MeteorSim } from "./routes";

class Creations extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  shouldComponentUpdate(nextProps, nextState) {
    return shouldSectionUpdate(this.props, nextProps, "creations");
  }
  render() {
    const curPageIndex = GlobalSelectors.findCurPageIndex(this.props.states.global);
    const activeIndex = curPageIndex + 1;
    return (
      <Slide axis={"horizontal"} activeIndex={activeIndex}>
        <div id="Creations">
          <PageNavigation
            sectionKey="creations"
            pageKey={null}
            actions={this.props.actions}
            states={this.props.states}
          />
          <div className="section-content">
            <SectionTitle
              title={this.props.localizations.Creations.title}
              descriptions={this.props.localizations.Creations.descriptions}
            />
          </div>
        </div>
        <Election2016 states={this.props.states} actions={this.props.actions} />
        <Dashboard states={this.props.states} actions={this.props.actions} />
        <MeteorSim states={this.props.states} actions={this.props.actions} />
      </Slide>
    );
  }
}

Creations.propTypes = {
  states: PropTypes.shape({
    global: PropTypes.object.isRequired
  }),
  actions: PropTypes.shape({
    global: PropTypes.object.isRequired
  })
};

Creations.defaultProps = {};
Creations.componentName = "Creations";

export default LocalizationEnhancer(["Creations"])(Creations);
